import React from "react";
import "./Confirm.sass";

const Confirm = ({ closeChat, closeConfirm }) => {
  return (
    <div className="confirm">
      <div className="confirm__question">
        Are you sure you want to end the conversation?
      </div>
      <div className="confirm__buttons">
        <button className="button button__red" onClick={closeConfirm}>
          No
        </button>
        <button className="button button__white" onClick={closeChat}>
          Yes
        </button>
      </div>
    </div>
  );
};

export default Confirm;
